import React from 'react'
import { Layout, Breadcrumb, Row, Col, Divider, Empty } from 'antd'
import { HomeOutlined } from '@ant-design/icons'
import { Tree } from 'antd';
import CodeDetail from './CodeDetail';

import axiosUtil from '../../utils/axiosUtil'

const { DirectoryTree } = Tree;
const { Content } = Layout

const updateTreeData = (list, key, children) => {
    return list.map((node) => {
        if (node.key === key) {
            return { ...node, children };
        } else if (node.children) {
            return { ...node, children: updateTreeData(node.children, key, children) };
        }
        return node;
    });
}

export default class CodeLibrary extends React.Component {


    constructor(props) {
        super(props)
        this.state = {
            treeData: [],
            detailId: -1,
            empty: true,
            loadMsg: "目录为空"
        }
    }
    componentDidMount() {
        axiosUtil.getNodesById(0).then((resp) => {
            if (resp.data.result.length > 0) {
                this.setState({ treeData: resp.data.result, empty: false })
            } else {
                this.setState({ loadMsg: "暂无数据" })
            }
        }).catch(err => this.setState({ empty: true, loadMsg: "数据加载失败" }))
    }
    onSelect = (selectedKeys, e) => {
        // console.log(selectedKeys);
        //处理列表项选择
        if (e.selectedNodes[0].isLeaf) {
            this.setState({
                detailId: -selectedKeys[0]
            })
        }
    }
    onExpand = (selectedKeys, e) => {
        //处理列表展开

    }

    onLoadData = ({ key, children }) => {
        // 异步数据获取
        return new Promise((resolve) => {
            if (children) {
                resolve();
                return;
            }
            // axios获取数据
            axiosUtil.getNodesById(key).then(resp => {
                // 更新结点
                this.setState({ treeData: updateTreeData(this.state.treeData, key, resp.data.result), })
                resolve();
            })
        });
    }
    render() {
        return (
            <Content style={{ margin: '0 16px' }}>
                <Breadcrumb style={{ margin: '16px 0' }}>
                    <Breadcrumb.Item><HomeOutlined /></Breadcrumb.Item>
                    <Breadcrumb.Item>代码库</Breadcrumb.Item>
                    <Breadcrumb.Item>公开库</Breadcrumb.Item>
                </Breadcrumb>

                <div className="site-layout-background" style={{ padding: 24, minHeight: 360 }}>
                    <h1>公开库查询</h1>
                    <Divider />
                    <Row>
                        <Col xs={24} md={24} lg={8}>
                            <Empty
                                style={{ marginTop: '10vh' }}
                                hidden={!this.state.empty}
                                image={Empty.PRESENTED_IMAGE_SIMPLE}
                                description={this.state.loadMsg}
                            />
                            <DirectoryTree
                                onSelect={this.onSelect}
                                onExpand={this.onExpand}
                                treeData={this.state.treeData}
                                loadData={this.onLoadData}
                            />

                        </Col>
                        <Col xs={24} lg={16}>
                            <CodeDetail id={this.state.detailId} />
                        </Col>
                    </Row>
                </div>
            </Content>
        )
    }
}