import React from 'react'
import { Layout, Descriptions, Divider, Typography, Empty, Button, message } from 'antd';
import { LightAsync as SyntaxHighlighter } from 'react-syntax-highlighter';
import { anOldHope } from 'react-syntax-highlighter/dist/esm/styles/hljs';
import copy from 'copy-to-clipboard';
import axiosUtil from '../../../utils/axiosUtil'
import moment from 'moment'

export default class CodeDetail extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            data: {
                author: "",
                code: {
                    id: 0,
                    name: "",
                    authorId: 0,
                    categoryId: 0,
                    code: "",
                    createTime: "",
                    frontModules: "",
                    manual: "",
                    updateTime: "",
                    version: "",
                    format: ""
                }
            }
        }
    }
    copyTo = () => {
        if (copy(this.state.data.code.code)) {
            message.success("复制成功 使用Ctrl+V粘贴")
        } else {
            message.warning("复制失败 您的浏览器不支持自动复制")
        }
    }
    errorFeedback = () => {
        message.warning("别着急，这个功能还在开发呢~")
    }
    componentDidMount() {
        if (this.props.id !== -1) {
            axiosUtil.getCodeById(this.props.id).then(resp => {
                this.setState({ data: resp.data.result })
            })
        }
    }
    componentDidUpdate(before) {
        if (this.props.id !== before.id) {
            axiosUtil.getCodeById(this.props.id).then(resp => {
                this.setState({ data: resp.data.result })
            })
        }
    }
    render() {
        return (
            <>
                <Empty
                    style={{ marginTop: '10vh' }}
                    hidden={this.props.id !== -1 ? true : false}
                    image={Empty.PRESENTED_IMAGE_SIMPLE}
                    description="请选择模块"
                />
                <Layout.Content
                    style={{ margin: '16px' }}
                    hidden={this.props.id === -1 ? true : false}
                >
                    <Descriptions
                        title={this.state.data.code.name}
                        size="small"
                    >
                        <Descriptions.Item label="作者"><Typography.Link>@{this.state.data.author}</Typography.Link></Descriptions.Item>
                        <Descriptions.Item label="日期">{moment(this.state.data.code.createTime).format("yyyy-MM-DD")}</Descriptions.Item>
                        <Descriptions.Item label="版本">{this.state.data.code.version}</Descriptions.Item>
                        <Descriptions.Item label="引用">{this.state.data.code.referId ? "referId" : "无"}</Descriptions.Item>
                        <Descriptions.Item label="转载">{this.state.data.code.reprint ? "转载" : "首次上传"}</Descriptions.Item>
                        <Descriptions.Item label="编号"><Typography.Link href={"/asdf" + this.props.id}>{this.props.id}</Typography.Link></Descriptions.Item>

                    </Descriptions>
                    <Divider />
                    <Descriptions
                        bordered
                        size="small"
                        column={1}
                    >
                        <Descriptions.Item label="使用说明">
                            {this.state.data.code.manual}
                        </Descriptions.Item>
                        <Descriptions.Item label="前置模块">
                            {this.state.data.code.frontModules ? this.state.data.code.frontModules : "无"}
                        </Descriptions.Item>
                        <Descriptions.Item label="操作">
                            <Button type="primary" style={{ marginRight: 3 }} onClick={this.copyTo}>复制到剪贴板</Button>
                            <Button type="danger" onClick={this.errorFeedback}>错误反馈</Button>
                        </Descriptions.Item>
                    </Descriptions>
                    <SyntaxHighlighter language={this.state.data.code.format} style={anOldHope}>
                        {this.state.data.code.code}
                    </SyntaxHighlighter>

                </Layout.Content>
            </>
        )
    }
}