import React from 'react'
import { message, Layout, Breadcrumb, Carousel, Row, Col, Statistic, Card, Divider, Empty, Alert } from 'antd'
import { HomeOutlined, LikeOutlined, StarOutlined, FileSearchOutlined, UnorderedListOutlined, NotificationOutlined, BulbOutlined } from '@ant-design/icons'
import DashboardData from './dashboard.json'

const { Content } = Layout

const contentStyle = {
    height: '160px',
    color: '#fff',
    lineHeight: '160px',
    textAlign: 'center',
    background: '#364d79',
};

const { Countdown } = Statistic;
const deadlineFormat = "D 天 H 时 m 分 s 秒"
export default class Dashboard extends React.Component {
    render() {
        return (
            <Content style={{ margin: '0 16px' }}>
                <Breadcrumb style={{ margin: '16px 0' }}>
                    <Breadcrumb.Item><HomeOutlined /></Breadcrumb.Item>
                    <Breadcrumb.Item>仪表盘</Breadcrumb.Item>
                </Breadcrumb>
                <div className="site-layout-background" style={{ padding: 24, minHeight: 360 }}>
                    <Alert message={<span><NotificationOutlined /> {DashboardData.notice}</span>} type="info" style={{ marginBottom: 24 }} />
                    <Row style={{ marginBottom: "32px" }}>

                        <Col span={24}>
                            <Carousel autoplay>
                                <div onClick={() => { message.info('推广合作Email：ops@wanvale.com') }}>
                                    <h3 style={contentStyle}>等待添加</h3>
                                </div>
                                <div onClick={() => { message.info('推广合作Email：ops@wanvale.com') }}>
                                    <h3 style={contentStyle}>等待添加</h3>
                                </div>
                                <div onClick={() => { message.info('推广合作Email：ops@wanvale.com') }}>
                                    <h3 style={contentStyle}>等待添加</h3>
                                </div>
                            </Carousel>
                        </Col>
                    </Row>
                    <Row gutter={16}>
                        <Col xs={12} md={6}>
                            <Statistic title="收录数" value={DashboardData.overview.indexed} prefix={<UnorderedListOutlined />} />
                        </Col>
                        <Col xs={12} md={6}>
                            <Statistic title="公开数" value={DashboardData.overview.public} prefix={<FileSearchOutlined />} />
                        </Col>
                        <Col xs={12} md={6}>
                            <Statistic title="收藏数" value={DashboardData.overview.favorites} prefix={<StarOutlined />} />
                        </Col>
                        <Col xs={12} md={6}>
                            <Statistic title="点赞数" value={DashboardData.overview.likes} prefix={<LikeOutlined />} />
                        </Col>
                    </Row>
                    <Divider />
                    <Row gutter={16}>

                        <Col xs={24} md={12} style={{ marginBottom: 16 }}>
                            <Card size="small" title="Top Lists">
                                <Empty
                                    image="https://gw.alipayobjects.com/zos/antfincdn/ZHrcdLPrvN/empty.svg"
                                    imageStyle={{
                                        height: 120,
                                    }}
                                    description={
                                        <span>
                                            No Data
                                        </span>
                                    }
                                >
                                </Empty>
                            </Card>
                        </Col>
                        <Col xs={24} md={12}>
                            <Card size="small" title="Deadline">
                                {DashboardData.deadline.map((item, index) => {
                                    if (item.color) {
                                        return (<Countdown key={index} title={item.title} prefix={<BulbOutlined />} value={new Date(item.dt)} format={deadlineFormat} style={{ marginBottom: 16 }} valueStyle={{ color: item.color }} />)
                                    } else {
                                        return (<Countdown key={index} title={item.title} value={new Date(item.dt)} format={deadlineFormat} style={{ marginBottom: 16 }} />)
                                    }
                                })}
                            </Card>

                        </Col>
                    </Row>
                </div>
            </Content>
        )
    }
}