import React from 'react'
import { Form, Input, Button, Checkbox, Row, Col, message } from 'antd';
import { MailOutlined, LockOutlined } from '@ant-design/icons';
import thLogo from '../../static/img/logo.png'
export default class Login extends React.Component {

    render() {
        const onFinish = (values) => {
            console.log(values);
            message.info("别急，这个功能正在做~")
        };
        return (
            <Row style={{ paddingTop: '10%', paddingBottom: '5%' }}>
                {/* <Col xs={0} md={6}/> */}
                <Col xs={24} md={12} style={{ backgroundColor: "#FFF", padding: 40, maxWidth: 480, margin: '0 auto', borderRadius: '40px' }}>
                    <p align="center" style={{ padding: 20 }}><img src={thLogo} width='50%' alt="logo" /></p>
                    <Form
                        name="normal_login"
                        className="login-form"
                        initialValues={{ remember: true }}
                        onFinish={onFinish}
                    >
                        <Form.Item
                            name="username"
                            rules={[{ required: true, message: '请输入Email' }]}
                        >
                            <Input
                                prefix={<MailOutlined
                                    className="site-form-item-icon" />}
                                placeholder="Email"
                                size="large"
                            />
                        </Form.Item>
                        <Form.Item
                            name="password"
                            rules={[{ required: true, message: '请输入密码' }]}
                        >
                            <Input
                                prefix={<LockOutlined className="site-form-item-icon" />}
                                type="password"
                                placeholder="密码"
                                size="large"
                            />
                        </Form.Item>
                        <Form.Item>
                            <Form.Item name="remember" valuePropName="checked" noStyle>
                                <Checkbox>自动登录（公共设备请勿勾选）</Checkbox>
                            </Form.Item>
                        </Form.Item>

                        <Form.Item>
                            <Button type="primary" block="block" htmlType="submit" shape="round">
                                登录
                            </Button>

                        </Form.Item>
                    </Form>
                </Col>
            </Row>
        )
    }
}