//SideNavBar
import React from 'react'
import { Layout, Menu } from 'antd';
import { NavLink as Link, withRouter } from 'react-router-dom'

import {
    DashboardOutlined,
    SelectOutlined,
    CodeOutlined,
    UserOutlined
} from '@ant-design/icons';
const { Sider } = Layout;
const { SubMenu } = Menu;
class SideNavBar extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            collapsed: true,
            title: "</>"
        };
    }
    flag = true
    onCollapse = collapsed => {
        if (!collapsed) {
            this.setState({ title: this.props.title })
        } else {
            this.setState({ title: this.props.sTitle })
        }
        this.setState({ collapsed });
    };

    getDfk = () => {
        if (!this.props.collapsed) {
            return []
        } else {
            return [this.path.match(/\/(.*)\//) == null ? "" : this.path.match(/\/(.*)\//)[1]]
        }
    }

    path = this.props.location.pathname;
    keys = ["common"]
    render() {
        const { collapsed } = this.state;
        return (
            <Sider collapsible collapsed={collapsed} onCollapse={this.onCollapse}>
                <div className="logo" ><Link to="/dashboard" className="text-white">{this.state.title}</Link></div>
                <Menu theme="dark" mode="inline" selectedKeys={this.props.location.pathname} defaultOpenKeys={this.getDfk()}>
                    <Menu.Item key="/dashboard" icon={<DashboardOutlined />}>
                        <Link to="/dashboard" >仪表盘</Link>
                    </Menu.Item>
                    {/* <Menu.Item key="/common/changelog" icon={<InfoCircleOutlined />}>
                        <Link to="/common/changelog">更新日志</Link>
                    </Menu.Item> */}
                    <SubMenu key="code" icon={<CodeOutlined />} title="代码库" >
                        <Menu.Item key="/cl/public">
                            <Link to="/cl/public">公开库</Link>
                        </Menu.Item>
                    </SubMenu>
                    <SubMenu key="common" icon={<SelectOutlined />} title="公共查询" >
                        <Menu.Item key="/common/changelog">
                            <Link to="/common/changelog">更新日志</Link>
                        </Menu.Item>
                    </SubMenu>
                    <SubMenu key="user" icon={<UserOutlined />} title="用户中心" >
                        <Menu.Item key="/user/login">
                            <Link to="/user/login">用户登录</Link>
                        </Menu.Item>
                    </SubMenu>
                </Menu>

            </Sider>
        )
    }
}

export default withRouter(SideNavBar)