import React from 'react'
import { message, Layout, Input, Select, BackTop } from 'antd';
import SideNavBar from '../SideNavBar'
import '../../style.css'
import { Route, HashRouter as Router, Switch, Redirect } from 'react-router-dom';
import ChangeLog from '../ChangeLog';
import Dashboard from '../Dashboard';
import CodeLibrary from '../CodeLibrary'
import Login from '../Login'
import axiosUtil from '../../utils/axiosUtil'

const { Header, Footer } = Layout;
const { Option } = Select





export default class Index extends React.Component {
    componentDidMount() {
        console.log("%c       ", "height:200px;font-size:41px;background:url('data:image/png;base64,') no-repeat ")
        console.log("%cThCode https://code.thsk.org", "width:320px;color:white;height: 18px;line-height:18px;font-size:12px;padding-left:5px;background-color: red; /* 不支持线性的时候显示 */background-image: linear-gradient(to right,#FF9999 ,80%,white);");
        axiosUtil.getConfigByName("siteTitle|siteTitleShort|siteName|partners").then(resp => {
            console.log(resp.data);
            if (resp.data.code === 200) {
                let newState = {}
                resp.data.result.map(item => {
                    if (item.confName === "partners") {
                        return newState['partners'] = JSON.parse(item.confValue)
                    } else {
                        return newState[item.confName] = item.confValue
                    }
                })
                console.log(newState);
                this.setState(newState)
                // this.setState({ partners: JSON.parse(resp.data.result.confValue) })
            }
        })
    }

    state = {
        searchType: "title",
        partners: [],
        siteTitle: "",
        siteTitleShort: "",
        siteName: ""
    }
    setSearchType = (val) => {
        this.setState({
            searchType: val
        })
    }

    searchTypeSelect =
        <Select defaultValue="title" className="select-before" onChange={(val) => this.setSearchType(val)}>
            <Option value="title">标题检索(模糊)</Option>
            <Option value="content">内容检索(模糊)</Option>
            <Option value="author">作者检索(模糊)</Option>
            <Option value="module">模块名检索(精确)</Option>
        </Select>

    render() {

        return (
            <Router>

                <Layout style={{ minHeight: '100vh' }}>
                    <SideNavBar title={this.state.siteTitle} sTitle={this.state.siteTitleShort} />
                    <Layout className="site-layout">
                        <Header className="site-header-background">
                            {/* <NotificationOutlined style={{ marginRight: "1%" }} /> */}

                            <Input.Search
                                addonBefore={this.searchTypeSelect}
                                placeholder="请输入关键字"
                                allowClear
                                size="large"
                                onSearch={() => { message.info(<span>检索类型 '{this.state.searchType}' 不可用</span>) }}
                                enterButton
                                style={{ paddingTop: '12px', paddingRight: '0px' }}
                            />

                        </Header>

                        <Switch>
                            <Route exact path="/user/login" component={Login} />
                            <Route exact path="/dashboard" component={Dashboard} />
                            <Route exact path="/common/changelog" component={ChangeLog} />
                            <Route exact path="/cl/public" component={CodeLibrary} />
                            <Redirect from="/" to="/dashboard" />
                        </Switch>
                        <Footer style={{ textAlign: 'center' }}><small>
                            合作伙伴 {this.state.partners.map((item, index) => { return (<span key={index}> / <a href={item.link}>{item.title}</a></span>) })}
                            <br />
                            &copy;2021 {this.state.siteName}.</small>
                        </Footer>
                    </Layout>
                    <BackTop />
                </Layout>
            </Router >
        );
    }
}