import React from 'react'
import { Layout, Breadcrumb, Row, Col, Timeline, Card, Divider, Badge, Pagination } from 'antd'
import { HomeOutlined } from '@ant-design/icons'
import axiosUtil from '../../utils/axiosUtil'

const { Content } = Layout
export default class ChangeLog extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            current: 0,
            data: [],
            total: 0,
            size: 0,
            pages: 0
        }
    }

    componentDidMount() {
        this.updateData()
    }

    updateData = (current) => {
        if (!current) current = 1
        axiosUtil.getChangelogByPage(current).then((resp) => {
            console.log(resp.data.result);
            this.setState({
                data: resp.data.result.records,
                size: resp.data.result.size,
                current: resp.data.result.current,
                pages: resp.data.result.pages,
                total: resp.data.result.total
            })
        })
    }


    render() {
        return (
            <Content style={{ margin: '0 16px' }}>
                <Breadcrumb style={{ margin: '16px 0' }}>
                    <Breadcrumb.Item><HomeOutlined /></Breadcrumb.Item>
                    <Breadcrumb.Item>公共查询</Breadcrumb.Item>
                    <Breadcrumb.Item>更新日志</Breadcrumb.Item>
                </Breadcrumb>
                <div className="site-layout-background" style={{ padding: 24, minHeight: 360 }}>
                    <Row>
                        <Col xs={0} sm={8} />
                        <Col xs={24} lg={8}>
                            <h1>Change log</h1>
                            <Divider />
                            <Timeline>
                                {this.state.data.map((item, index) => {
                                    return (
                                        <Timeline.Item key={item.id} color={index === 0 ? "blue" : index === (this.state.size - 1) ? "green" : "gray"}>
                                            <Card>
                                                <p>{item.version}</p>
                                                <p style={{ whiteSpace: 'pre-wrap' }}>
                                                    {item.content}
                                                </p>
                                                <Badge color={item.type.indexOf("func") === -1 ? "gray" : "blue"} text="功能更新" />
                                                <Divider type="vertical" />
                                                <Badge color={item.type.indexOf("view") === -1 ? "gray" : "green"} text="界面更新" />
                                                <Divider type="vertical" />
                                                <Badge color={item.type.indexOf("bugfix") === -1 ? "gray" : "red"} text="问题修复" />
                                            </Card>
                                        </Timeline.Item>
                                    )
                                })}
                            </Timeline>
                            <div style={{ textAlign: 'center' }}>
                                <Pagination
                                    current={this.state.current}
                                    total={this.state.total}
                                    hideOnSinglePage={true}
                                    onChange={page => {
                                        this.updateData(page)
                                    }}
                                />
                            </div>
                        </Col>
                    </Row>
                </div>
            </Content>
        )
    }
}